import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class DatatypeMapingService {

  dataTypes = [
    'string',
    'bool',
    'decimal',
    'DateTime'
  ];

  constructor() { }



  public csharpToTypescript(dataType: string): string {
    let response = "";
    let tempDataType = dataType.trim().toLowerCase();
    if (tempDataType.match(/^string$/)) {
      response = "string";
    } else if (tempDataType.match(/^bool$/)) {
      response = 'boolean';
    } else if (tempDataType.match(/^decimal$/)) {
      response = 'decimal';
    } else if (tempDataType.match(/^DateTime$/)) {
      response = 'DateTime'
    } else {
      // Do Nothing
    }
    return response;
  }

  public csharpToMsSql(dataType: string): string {
    let response = "";
    let tempDataType = dataType.trim().toLowerCase();
    if (tempDataType.match(/^string$/)) {
      response = "varchar(max)";
    } else if (tempDataType.match(/^bool$/)) {
      response = 'bit';
    } else if (tempDataType.match(/^decimal$/)) {
      response = 'decimal(18, 4)';
    } else if (tempDataType.match(/^DateTime$/)) {
      response = 'DateTime'
    } else {
      // Do Nothing
    }
    return response;
  }

}
