import { Component, Input, OnInit, Output } from '@angular/core';
import { FormArray, FormGroup } from '@angular/forms';
import { UsformsService } from 'src/app/usforms.service';
import { debounceTime } from "rxjs/operators";
import { DatatypeMapingService } from 'src/app/datatype-maping.service';
import { dataTypeMap } from 'src/app/models/datatypeMap';

@Component({
  selector: 'app-class-maker',
  exportAs: 'classMaker',
  templateUrl: './class-maker.component.html',
  styleUrls: ['./class-maker.component.less']
})
export class ClassMakerComponent implements OnInit {

  form: FormGroup = this.usForms.class(null);
  @Input() language!: string;
  @Output() onChange = this.form.valueChanges.pipe(debounceTime(500));
  dataTypes!: Array<any>;

  constructor(
    private usForms: UsformsService,
    private dataTypeMaping: DatatypeMapingService
  ) {

  }

  ngOnInit(): void {
    this.dataTypes = this.dataTypeMaping.dataTypes;
    console.log(this.language);

  }

  addField(): void {
    let field = this.usForms.field(null);
    let formArray = this.fields;
    formArray.push(field);
  }

  get fields(): FormArray {
    return this.form.get('fields') as FormArray;
  }

  formGroup(index: number): FormGroup {
    return this.fields.controls[index] as FormGroup;
  }

  removeField(form: FormGroup): void {
    let index = this.fields.controls.findIndex(el => el == form);
    this.fields.controls.splice(index, 1);
    this.fields.updateValueAndValidity();
  }

}
