import { Component, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { debounceTime } from 'rxjs/operators';
import { ClassToCodeService } from 'src/app/class-to-code.service';
import { Class } from 'src/app/models/class';

@Component({
  selector: 'app-visual-coding',
  templateUrl: './visual-coding.component.html',
  styles: [
  ]
})
export class VisualCodingComponent implements OnInit {

  code: string = "";
  language: string = "";
  technology: FormControl = new FormControl('reactive-forms', [Validators.required]);
  editorOptions = { language: 'javascript', readonly: true };
  modelClass!: Class;

  constructor(
    private classToCode: ClassToCodeService
  ) { }

  ngOnInit(): void {
    this.registerChanges();
  }

  private registerChanges(): void {
    this.technology.valueChanges.subscribe(_ => {
      this.createCode();
    });
  }

  private createCode(): void {

    switch (this.technology.value) {
      case "reactive-forms":
        this.editorOptions.language = "typescript";
        this.code = this.classToCode.angularReactiveForms(this.modelClass);
        break;
      case "entity-framework":
        this.editorOptions.language = "csharp";
        this.code = this.classToCode.entityFramework(this.modelClass);
        break;
      case "mssql":
        this.editorOptions.language = "sql";
        this.code = this.classToCode.mssql(this.modelClass);
        break;
      case "typescript":
        this.editorOptions.language = "typescript";
        this.code = this.classToCode.typescript(this.modelClass);
        break;
    }
  }

  convertToCode(model: Class): void {
    this.modelClass = model;
    this.createCode();
  }
}
