import { Injectable } from '@angular/core';
import { DatatypeMapingService } from './datatype-maping.service';
import { Class } from './models/class';

@Injectable({
  providedIn: 'root'
})
export class ClassToCodeService {

  constructor(
    private dataTypeMaping: DatatypeMapingService
  ) { }

  angularReactiveForms(form: Class): string {
    return `
    create${form.title?.replace(" ", "")}Form(data: any) : FormGroup { 
      let form = new FormGroup({
        ${form.fields?.map(el => el.title?.replace(" ", "") + ": new FormControl(null" + (el.required == true ? ",[Validators.required]" : "") + ")").join(",\n\t\t")}
      });

      if(data != null){
        form.patchValue(data);
      }

      return form;
    }
    `
  }

  entityFramework(form: Class): string {
    return `
      class ${form.title?.replace(" ", "")} {
        ${form.fields?.map(el => "public " + el.dataType + " " + el.title?.replace(" ", "") + " {get; set;}").join(";\n\t\t")}
      }
    `
  }

  mssql(form: Class): string {
    return `
      CREATE TABLE [dbo].[${form.title?.replace(" ", "")}] (
        ${form.fields?.map(el => "[" + el.title?.replace(" ", "") + "] " + this.dataTypeMaping.csharpToMsSql(el.dataType) + (el.required ? " NOT NULL " : " NULL ")).join(",\n\t\t")}
      )
    `
  }

  typescript(form: Class): string {
    return `
      export class ${form.title?.replace(" ", "")} (
        ${form.fields?.map(el => el.title + " : " + this.dataTypeMaping.csharpToTypescript(el.dataType)).join(",\n\t\t")},

        constructor(${form.fields?.map(el => el.title + "?: " + this.dataTypeMaping.csharpToTypescript(el.dataType)).join(", ")})
        {
          ${form.fields?.map(el => "this." + el.title + " = " + el.title + ";").join("\n\t\t")}
        }
      )
    `
  }
}
